/* stylelint-disable no-descending-specificity */
/* stylelint-disable selector-class-pattern */
@import '~assets/styles/h3Imports';

.rdp-day_selected {
  background-color: #00bcd4;
  color: white;
}

.DayPicker {
  width: 100%;
}

// .DayPicker-Day--monday {
//   color: #00bcd4;
// }

.DayPicker-Caption {
  display: none;
}

.DayPicker-Month {
  margin-left: 0;
  margin-right: 0;
  width: 100%;

  [data-yearopen='true'] & {
    display: none;
  }
}

@media #{$tablet-down} {
  [data-fontsize='AA'] .DayPicker-Weekday,
  [data-fontsize='AAA'] .DayPicker-Weekday {
    padding: 0;
  }
}

@media (max-width: 500px) {
  [data-fontsize='AAA'] .DayPicker-Weekday {
    font-size: 0.65em;
  }
}

.DayPicker-Week {
  position: relative;
}

.DayPicker-Day {
  @include h5;

  border-radius: 0;
  color: var(--picker-day-color);
  height: rem(46);
  line-height: 1;
  padding: 0;
  position: relative;
  width: rem(50);

  @media #{$tablet-down} {
    [data-fontsize='AA'] &,
    [data-fontsize='AAA'] & {
      height: auto;
      width: auto;
    }
  }

  .day-dot {
    span {
      background-color: transparent;
      border-radius: 50%;
      content: '';
      display: block;
      height: 5px;
      left: 50%;
      position: absolute;
      transform: translate(-50%, 1px);
      width: 5px;
    }
  }

  &--outside {
    color: var(--picker-day-outside-color);

    &.DayPicker-Day--appointments {
      .day-dot span {
        background-color: var(--picker-day-outside-color);
      }
    }
  }

  &--today {
    .day-wrapper {
      border: 2px solid #606565;
    }
  }

  &--appointments {
    .day-dot {
      span {
        background-color: var(--picker-day-color);
      }
    }
  }

  &--disabled {
    color: var(--picker-day-disabled);

    &.DayPicker-Day--appointments {
      .day-dot span {
        background-color: var(--picker-day-disabled);
      }
    }
  }
}

// Hover
.DayPicker:not(.DayPicker--interactionDisabled)
  .DayPicker-Day:not(.DayPicker-Day--disabled):not(.DayPicker-Day--selected):not(.DayPicker-Day--outside):hover {
  background: none;

  .day-wrapper {
    background-color: var(--picker-day-hover);
  }
}

.DayPicker:not(.DayPicker--interactionDisabled)
  .DayPicker-Day:not(.DayPicker-Day--selected):not(.DayPicker-Day--outside):focus {
  outline: none;

  .day-wrapper {
    outline: auto;
  }
}

.DayPicker:not(.DayPicker--interactionDisabled)
  .DayPicker-Day:not(.DayPicker-Day--selected):not(.DayPicker-Day--outside):focus-visible {
  outline: none;

  .day-wrapper {
    outline: 2px solid var(--focus-button-outline-color, #5995b3);
    outline-offset: 2px;
  }
}

.DayPicker:not(.DayPicker--interactionDisabled)
  .DayPicker-Day:not(.DayPicker-Day--selected):not(.DayPicker-Day--outside):not(:focus-visible) {
  outline: none;

  .day-wrapper {
    outline: none;
  }
}

.DayPicker:not(.DayPicker--interactionDisabled)
  .DayPicker-Day:not(.DayPicker-Day--disabled):not(.DayPicker-Day--selected):not(.DayPicker-Day--outside):active {
  .day-wrapper {
    background-color: var(--picker-day-pressed-background);
  }
}

.DayPicker-Day--selected:not(.DayPicker-Day--disabled):not(.DayPicker-Day--outside) {
  background: none;
  outline: none;

  .day-wrapper {
    background-color: var(--picker-day-active-background);
  }

  &:focus {
    .day-wrapper {
      outline: auto;
    }
  }

  &.DayPicker-Day--appointments {
    .day-dot span {
      background-color: #ffffff;
    }
  }

  &:hover {
    background: none;
  }

  &:focus-visible {
    .day-wrapper {
      outline: 2px solid var(--focus-button-outline-color, #5995b3);
      outline-offset: 2px;
    }
  }
}

[data-yearopen='true'] {
  [data-button='month-prev'],
  [data-button='month-next'] {
    display: none;
  }

  .DayPicker-NavBar {
    justify-content: center;
  }
}

