@import '~assets/styles/h3Imports';

.year-wrapper {
  border-radius: 40px;
  color: var(--picker-day-color);
  margin: 3px;
  padding: 10px 8px;
  text-align: center;
  @include h5;

  @media (max-width: 600px) {
    [data-fontsize='AAA'] & {
      padding: 10px 5px;
    }
  }
}

.year-cell {
  flex: 0 1 25%;
  padding-top: 4px;

  &:hover {
    .year-wrapper {
      background-color: var(--picker-day-hover);
      cursor: pointer;
    }
  }

  &:focus {
    outline: none;

    .year-wrapper {
      outline: auto;
    }
  }

  &:focus-visible {
    outline: none;

    .year-wrapper {
      outline: 2px solid var(--focus-button-outline-color, #5995b3);
      outline-offset: 2px;
    }
  }

  &[aria-selected='true'] {
    .year-wrapper {
      background: var(--picker-day-active-background);
      color: var(--picker-day-active-color);
    }
  }

  &:focus:not(:focus-visible) {
    outline: none;

    .year-wrapper {
      outline: none;
    }
  }
}
