@import '~assets/styles/h3Imports';

.picker-navbar {
  align-items: center;
  display: flex;
  height: rem(48);
  justify-content: space-between;
  padding-bottom: 1rem;
  position: relative;

  @media #{$tablet-down} {
    [data-fontsize='AAA'] & {
      height: rem(52);
    }
  }

  &::after {
    background: #323264;
    bottom: 0;
    content: '';
    height: 1px;
    left: 0;
    opacity: 0.16;
    position: absolute;
    width: 100%;
  }
}

.month-year {
  @include h4;

  align-items: center;
  background: transparent;
  border: none;
  border-radius: 8px;
  color: var(--date-picker-heading);
  display: flex;
  margin: 0;
  overflow: hidden;
  padding: 8px;
  position: relative;

  [data-element='month-year-text']::after {
    background: url('../../../assets/icons/calendar-chevron-year.svg') no-repeat
      50% 50%;
    content: '';
    display: inline-block;
    height: 24px;
    margin-left: var(--spacing-xxxs);
    position: relative;
    top: 5px;
    transition: all 0.25s ease;
    width: 24px;
  }

  &[aria-expanded='true'] {
    [data-element='month-year-text']::after {
      background-image: url('../../../assets/icons/calendar-chevron-year-open.svg');
    }
  }

  &:hover {
    background: var(--text-button-hover-background);
  }

  &:focus-visible {
    outline: 2px solid var(--focus-button-outline-color, #5995b3);
    outline-offset: 2px;
  }

  @media (max-width: 600px) {
    [data-fontsize='AAA'] & {
      font-size: 0.85rem;
    }
  }
}

.month-button {
  background: none;
  border: none;
  border-radius: 8px;
  height: 2rem;
  padding: 0;
  width: 2rem;

  &:hover {
    background: #e5e5e5;
    background-color: var(--text-button-hover-background);
  }

  &:active {
    background-color: var(--text-button-pressed-background);
  }

  &:focus-visible {
    outline: 2px solid var(--focus-button-outline-color, #5995b3);
    outline-offset: 2px;
  }

  &[data-button='month-next'] {
    svg {
      transform: rotate(180deg);
    }
  }
}
