@import '~assets/styles/h3Imports';

.date-button {
  background: none;
  border: none;
  border-radius: 8px;
  height: rem(36);
  padding: 0;
  position: absolute;
  right: 7px;
  top: calc(1.625rem + 0.25em);
  width: rem(36);

  &:hover {
    background-color: var(--text-button-hover-background);
  }

  &:active {
    background-color: var(--text-button-pressed-background);
  }

  svg {
    left: 50%;
    position: absolute;
    top: 50%;
    transform: translate(-50%, -50%);
    width: rem(24);
  }
}

.daypicker-wrapper {
  background: #ffffff;
  border-radius: var(--border-radius);
  box-shadow: var(--box-shadow-modal);
  margin-top: 8px;
  max-width: var(--picker-max-width);
  padding: 16px;
  position: absolute;
  right: 0;
  width: 100%;
  z-index: 10;

  @media #{$tablet-up} {
    [data-fontsize='AA'] &,
    [data-fontsize='AAA'] & {
      min-width: 500px;
      width: auto;
    }

    [data-fontsize='AAA'] & {
      min-width: 600px;
    }
  }
  @media #{$tablet-down} {
    [data-fontsize='AA'] &,
    [data-fontsize='AAA'] & {
      --picker-max-width: auto;

      left: -30px;
      right: auto;
      width: calc(100% + 60px);
    }
  }
}

.day-picker-buttons {
  --button-padding: 10px;
  --bordered-buttons-padding-top: 9px;
  --bordered-buttons-padding-bottom: 9px;

  align-items: center;
  display: flex;
  justify-content: center;

  button,
  a {
    --button-min-width: 7.5rem; // 120px
    --button-font-size: 0.875rem; // 16px
    --box-shadow: none;

    height: auto;
  }
}

.day-wrapper {
  align-items: center;
  border-radius: 40px;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  height: rem(40);
  justify-content: center;
  margin: auto;
  width: rem(40);

  @media #{$tablet-down} {
    [data-fontsize='AA'] & {
      height: 40px;
      width: 40px;
    }
  }

  @media (max-width: 618px) {
    [data-fontsize='AAA'] & {
      height: 50px;
      width: auto;
    }
  }
}

.has-appointments {
  @include caption-text;

  margin: var(--spacing-xxxxs) 0 var(--spacing-s);
  text-align: center;
}

.appointment-dot {
  background-color: var(--picker-day-active-background);
  border-radius: 50%;
  display: inline-block;
  height: 6px;
  margin-right: 11px;
  position: relative;
  top: -1px;
  width: 6px;
}
