@import '~assets/styles/h3Imports';

@include text-inputs;

.date-field-wrapper {
  --max-width-input: 100%; //23.8125rem; //381px
  --input-label-width: 100%; //23.8125rem; //381px
  --icon-fill: var(--date-field-icon, var(--interface-info));

  button {
    svg {
      height: rem(24);
      width: rem(24);
    }
  }
}

.text-element {
  color: var(--text-color);
  margin-right: 0;

  --min-width-input: 1%;

  &:focus {
    &::placeholder {
      color: transparent;
    }
  }
}

.error {
  --icon-fill: var(--input-error);
}

.disabled {
  --icon-fill: var(--input-icon-disabled);
}

.readonly {
  --icon-fill: var(--input-icon-readonly);
}

.not-default:valid {
  color: var(--text-color);
}

.default:valid:hover {
  color: var(--input-default);
}

.date-field-wrap {
  position: relative;
}

.date-field {
  display: block;
  position: relative;
}

.date-picker-open {
  border: 2px solid var(--input-focus);
  padding: rem(12) rem(12) rem(12) 15px;

  &:hover:enabled {
    padding: rem(12) rem(12) rem(12) 15px;
  }
}

.label-text {
  display: inline-block;
  margin-bottom: 0.25em;
}

.mask {
  font-size: rem(16);
  font-weight: 600;
  left: 17px;
  opacity: 0;
  pointer-events: none;
  position: absolute;
  top: rem(12);
  z-index: 2;

  &[data-focus='true'][data-count='0'] {
    opacity: 1;
  }
}
