@import '~assets/styles/h3Imports';

.year-row {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: var(--spacing-xxxs);
  padding: 0 4px 4px;
}

.year-grid {
  margin: 0 -4px;
  max-height: rem(230); //rem(194);
  overflow-y: auto;
}

.year-grid-two-years {
  [role='gridcell'] {
    flex: 0 1 50%;
  }
}

.year-grid-three-years {
  [role='gridcell'] {
    flex: 0 1 33.3%;
  }
}

.day-picker-buttons {
  --button-padding: 10px;
  --bordered-buttons-padding-top: 9px;
  --bordered-buttons-padding-bottom: 9px;

  align-items: center;
  display: flex;
  justify-content: center;
  padding-top: 20px;

  button,
  a {
    --button-min-width: 7.5rem; // 120px
    --button-font-size: 0.875rem; // 16px
    --box-shadow: none;

    height: auto;
    margin-bottom: 0;
  }
}
